export default {
	data() {
		return {
			StatusAddNav:this.StatusAddNav,
			// 是否登录标志
			isLoginFlag: false,
			/******************/
			// banner
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 1000,
			userInfo:{
				collect_merchant:0,
				collect_product:0,
				phone:"",
			},
		};
	},
 
	onShow() {
		this.isLoginFlag=this.isLogin();
		setTimeout(()=>{
			this.getUserInfo();
		},800);
	},
	methods: {
		//获取用户的信息
		getUserInfo(){
			let that=this;
			that.basePost(
				that.U({ c: 'user_api', a: 'my'}),
				{},
				function(res) {
					that.userInfo=res.data
				},
				function(res) {
					console.log(res);
				}
			);
		},
		//跳转登录页面
		toLogin(){
			uni.navigateTo({
				url:"/pages/login/pwd_login/pwd_login"
			})
		},
		edit() {
			uni.navigateTo({
				url: '/pages/user/info_edit/info_edit'
			});
		},
		news(e) {
			uni.navigateTo({
				url: '/pages/user/my_news/my_news'
			});
		},
		mycol(e) {
			uni.navigateTo({
				url: '/pages/shop/my_collection/my_collection'
			});
		},
		shopcol(e) {
			uni.navigateTo({
				url: '/pages/shop/shop_collection/shop_collection'
			});
		},
		order(e) {
			console.log(e);
			uni.navigateTo({
				url: '/pages/user/my_order/my_order?index='+e
			});
		},
		coupon(e) {
			uni.navigateTo({
				url: '/pages/user/my_coupon/my_coupon'
			});
		},
		info(e) {
			uni.navigateTo({
				url: '/pages/user/my_info/my_info'
			});
		}
	}
};
